.footer {
    background-color: $main-bgcolor;

}
.footer-spitch {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    margin-bottom: 16px;
    padding-left: 2px;

    color: $modal-bgcolor;

    @media screen and(min-width:768px) {
        font-size: 26px;
        line-height: 1.23;
        letter-spacing: 0.04em;
        margin-bottom: 9px;

    }

    @media screen and(min-width:1200px) {
        font-weight: 400;
        font-size: 30px;
        line-height: 1.23;
        margin-bottom: 14px;
    }
}

.footer-spitch__part {
    font-weight: 700;
    font-size: 34px;
    line-height: 1.2;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    font-family: $main-font;

    color: $modal-bgcolor;

    @media screen and(min-width:768px) {
        font-size: 48px;
        line-height: 1.22;
        letter-spacing: 0.06em;
    }

    @media screen and(min-width:1200px) {
        font-size: 58px;
        line-height: 1.22;
    }
}

.socials {
    padding-bottom: 40px;

    @media screen and (min-width: 768px) {
        flex-direction: column;
        justify-content: center;
        // margin-top: auto;
        margin-left: auto;
        display: flex;
        padding-bottom: 56px;
        padding-top: 68px;
    }

    @media screen and (min-width: 1200px) {
        margin-top: 0;
        padding-bottom: 60px;
        padding-top: 0;
    }
}

.contact__title {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    color: $second-txcolor;
    text-align: right;
    font-family: $main-font;

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        letter-spacing: 0.02em;
    }
}

.contact__tel {
    color: $modal-bgcolor;
    padding: 3px;
    cursor: pointer;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        color: $eighth-txcolor;
    }

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        letter-spacing: 0.02em;
    }
}

.copiright__product {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: $main-font;

    color: $fifth-txcolor;

    @media screen and (min-width: 768px) {
        flex-direction: row;
        justify-content: center;
    }

    @media screen and (min-width: 1200px) {
        font-size: 12px;
    }
}

.footer__title {
    margin-bottom: 60px;
    font-family: $main-font;
    @media screen and(min-width:768px) {
        width: 352px;
        flex-direction: column;
        justify-content: center;
        display: flex;
        padding-bottom: 32px;
        margin-bottom: 0;
    }

    @media screen and(min-width:1200px) {
        width: auto;
        padding-bottom: 54px;
    }
}

.socials__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    background-color: $fifth-bgcolor;
    border-radius: 50%;
    fill: $main-bgcolor;
    cursor: pointer;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
        transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
        transform: scale(1.3);
    fill: $modal-bgcolor;}
}



.socials__list {
    display: flex;
    justify-content: flex-end;
    gap: 28px;
    margin-bottom: 40px;
    padding-bottom: 10px;
    padding-top: 7px;
    @media screen and(min-width:768px) {
    }

    @media screen and(min-width:1200px) {  }
}

.copiright {
    border-top: 1px solid #e1e1e1;
    padding-top: 16px;

    @media screen and(min-width:768px) {
        padding-top: 14px;
    }

    @media screen and(min-width:1200px) {
        padding-top: 12px;
    }
}

.wrapper {
    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row;
    }
}

.footer {
    padding-bottom: 20px;
    padding-top: 60px;

    @media screen and(min-width:768px) {
        padding-bottom: 12px;
        padding-top: 40px;
    }

    @media screen and(min-width:1200px) {
        padding-bottom: 12px;
        padding-top: 120px;
    }
}